* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: #ebebe0;
}

/* HEADER */

.header {
  color: white;
  background: #2c3e50;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0.8em;
  padding-top: 0.8em;
  padding-bottom: 0.8em;
  text-align: center;
}

a {
  color: white;
  text-decoration: none;
}

.logo {
  padding-left: 10px;
}

.links {
  font-size: .9em;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  position: absolute;
  right: 10px;
}

.links li:nth-of-type(2) {
  padding-left: 3px;
  padding-right: 3px;
}

/* SLIDESHOW */

.scroll-components, .still {
  display: -ms-flexbox;
  display: flex;
}

.prev {
  text-align: center;
  width: 25%;
}

.next {
  text-align: center;
  width: 25%;
}

.picture {
  text-align: center;
  width: 50%;
  height: 100%;
}

.scroll img {
  border: 3px solid #2c3e50;
  height: 100%;
  width: 100%;
}

button {
  border: none;
  font-size: 1em;
  padding: 2px 7px;
  border: 2px solid #2c3e50;
  border-radius: 12px;
  position: relative;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

button:active {
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}

#prev, #next {
  position: relative;
  top: 40%;
}

input {
  margin-bottom: 12px;
}

.settings {
  margin-bottom: 0.8em;
  text-align: center;
}

/* GALLERY VIEW */

.gallery-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-bottom: 50px;
}

.picture-preview {
  margin: 2rem auto;
  width: 90%;
  border: 3px solid #2c3e50;
  border-radius: 4px;
  text-align: center;
}

.thumbnail {
  cursor: pointer;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  padding-bottom: 5px;
  pointer-events: none
}

.gallery {
  width: 100%;
}

.gallery-view {
  margin-left: 48%;
  margin-bottom: 20px;
  font-size: 0.8em;
}

.gallery-view > ul{
  display: -ms-flexbox;
  display: flex;
  font-size: 2em;
  list-style: none;
  color: #2c3e50;
}

.gallery-view button:nth-of-type(2) {
  margin-left: 20px;
}

.current-picture {
  width: 80%;
  margin: 0 auto;
}

.current-picture img {
  width: 100%;
  border: 3px solid #2c3e50;
}

.thumbnail-list {
  margin: 0 auto;
}

.thumbnail-list img {
  width: 48px;
}

.thumbnail-bar {
  display: -ms-flexbox;
  display: flex;
}

.active-button {
  background: #2c3e50;
  color: white;
}

/* MODAL OVELAY */

.modal-overlay {
 position: fixed;
 top: 0;
 left: 0;
 z-index: 10  ;
 width: 100%;
 height: 100%;
 background: rgba(21,21,21,.75);
}

.modal-body {
 position: absolute;
 top: 50%;
 left: 50%;
 z-index: 11;
 padding: 0;
 overflow: auto;
 -webkit-transform: translate(-50%, -50%);
     -ms-transform: translate(-50%, -50%);
         transform: translate(-50%, -50%);
}

.modal-body img {
  width: 100%;
}

.modal-close {
 position: absolute;
 background-color: transparent;
 border: none;
 color: white;
 top: 0;
 right: 8px;
 font-size: 1.5rem;
 -webkit-transition: all .25s ease-in-out;
 -o-transition: all .25s ease-in-out;
 transition: all .25s ease-in-out;
 }

 .modal-close:focus, .modal-close:hover {
  color: #2c3e50;
 }

/* MEDIA QUERIES */

@media screen and (min-width: 500px) {
  .picture-preview {
    width: 40%;
  }

  .gallery-view {
    margin-left: 76%;
  }

  .header {
    font-size: 1.1em;
  }

  .thumbnail {
    padding-bottom: 0;
    pointer-events: auto;
  }

  .current-picture {
    width: 50%;
  }
}

@media screen and (min-width: 900px) {
  .header p {
    width: 20%;
    text-align: right;
  }

  .links {
    right: 15%;
  }

  .thumbnail-list img {
    width: 75px;
  }

  .picture-preview {
    width: 30%;
  }

  .header {
    font-size: 1.3em;
  }
}

@media screen and (min-width: 1200px) {
  .picture-preview {
    width: 20%;
  }
}



